import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CartService {
  items = [];

  addToCart(product) {
    this.items.push(product);
  }

  getItems() {
    return this.items;
  }

  clearCart(obj) {
     this.items = [];
    return this.items; 
    /* this.product=this.product.filter(item => item !==obj) */
  }
  constructor() { }
}
