import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { SaleComponent } from './sale/sale.component';
import { ProductComponent } from './product/product.component';
import { DbrdComponent } from './dbrd/dbrd.component';
import { DbrdhomeComponent } from './dbrdhome/dbrdhome.component';
import { SalesledgerComponent } from './salesledger/salesledger.component';


const routes: Routes = [
  { path:'home',component:HomeComponent},
  { path:'shop',component:SaleComponent},
  { path:'plist',component:ProductComponent},
  { path:'dbrd',component:DbrdComponent},
  { path:'dbrdhome',component:DbrdhomeComponent},
  { path:'dbsale',component:SalesledgerComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
