import { Component, OnInit } from '@angular/core';

export class MyProducts{
  Value:string;
  category:string;
  quantity:string;
  image:string;
  price:string;
  description:string;
  constructor(Value:string,category:string,quantity:string,image:string,price:string,description:string){
    this.Value = Value;
    this.category = category;
    this.quantity = quantity;
    this.image = image;
    this.price = price;
    this.description = description;
  }
}

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  catg:any[] = ['Biscuit','Oil','Noodles','Rice','Chocolate']
  myProducts:MyProducts[] = new Array();

  IsForUpdate:boolean = false;
  newProduct:any = {};
  updatedProduct;
  constructor() {
    this.myProducts.push(
      new MyProducts("Moms-Magic Biscuit","Biscuit","350","assets/images/1.jpg","35","dry-fruit mix 150 gm"),
      new MyProducts("Cinthol Soap","Soap","150","assets/images/20.jpg","185","8 pc worth 100 gm"),
      new MyProducts("Sunfeast Biscuit","Biscuit","450","assets/images/3.jpg","45","Sugar Free"),
      new MyProducts("Fortune Pure Oil","Oil","250","assets/images/5.jpg","750","15Ltr Drum"),
      new MyProducts("Maggiee","Noodles","1250","assets/images/16.jpg","28","Instant ready product")
    );
   }

  ngOnInit() {
  }
  AddItem() {    
    this.myProducts.push(    
      this.newProduct    
    );    
    this.newProduct = {};    
  }   
  
  
  EditItem(i) {  
    this.newProduct.Value = this.myProducts[i].Value;
    this.newProduct.category = this.myProducts[i].category;
    this.newProduct.quantity  = this.myProducts[i].quantity ; 
    this.newProduct.image  = this.myProducts[i].image ;
    this.newProduct.price  = this.myProducts[i].price ;
    this.newProduct.description  = this.myProducts[i].description ; 
    this.UpdatedItem = i;  
    this.IsForUpdate = true;  
  }  


  UpdatedItem() {  
    let data:any = this.UpdatedItem;  
    for (let i = 0; i < this.myProducts.length; i++) {  
      if (i == data) {  
        this.myProducts[i].Value = this.newProduct.Value;  
        this.myProducts[i].category = this.newProduct.category; 
        this.myProducts[i].quantity = this.newProduct.quantity;
        this.myProducts[i].image = this.newProduct.image;
        this.myProducts[i].price = this.newProduct.price;
        this.myProducts[i].description = this.newProduct.description;  
      }  
    }  
    this.IsForUpdate = false;  
    this.newProduct = {};  
  }  
  DeleteItem(i) {  
    this.myProducts.splice(i, 1);  
  }  

  
}
